<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>页面标题</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents('AdminButton')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents('AdminButton')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents('AdminButton')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents('AdminButton')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <div class="form-group">
                        <label class="control-label col-sm-2">类型名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入类型名,最多为30位'" v-model="addData.name" :data-vv-as="'类型名'"
                             v-validate="$api.opreate.validateAdd('name',30,stateid,opreate,'equipment')">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">类型说明</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('interpret')&&addData.interpret!='','has-error':errors.has('interpret')}">
                            <textarea rows="6" class="form-control" name="interpret" :placeholder="'请输入类型说明'" v-model="addData.interpret"
                             :data-vv-as="'类型说明信息'" v-validate="'required'">
                            </textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('interpret')">{{errors.first("interpret")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备厂商</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.manufacturer_id">
                                <option :value="item.manufacturerId" v-for="(item,index) of manufacturer" :key="index" v-text="item.manufacturerName"></option>
                                 </select>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
设备场景类别
<script>
import tables from "@/components/table.vue";
export default {
  data: function() {
    return {
      inforadmin: false,
      opreate: "add",
      tableSelectData: {
        selectTableurl: "",
        pageloginData: {
          id: this.stateid
        },
        filterName: []
      },
      addData: {}
    };
  },
  props: {
    stateid: String,
    statetitle: String
  },
  computed: {
    checkData() {
      return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
    }
  },
  components: {
    tables
  },
   mounted(){
        this.$store.commit("UPDATETABLESELECTDATA",this.tableSelectData);
    },
  methods: {
    //添加按钮按下
    inforAddClick(bool) {
      var vm = this;
      var opreate = this.opreate;
      if (bool) {
        vm.publicAddAndUpdate();
      }
      //修改状态下
      // if(opreate=='update'){
      //     this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]); //将选择中行的数据复制给addData，让form表单数据不为空
      // }
      // 异步函数，防止表单出现后存在错误，更改用户体验
      async function Empty() {
        //将addData数据修改为空
        for (var key in vm.addData) {
          vm.addData[key] = "";
        }
        return true;
      }
      Empty().then(() => {
        vm.opreate = "add";
        vm.inforadmin = bool;
        vm.$validator.errors.clear(); //将表单错误数组重置为0
      });
    },
    //添加修改公共函数,添加修改界面初始化均需要执行获取数据或做其他处理的函数可以放入其中
    publicAddAndUpdate() {
      var vm = this;
      vm.get1();
      vm.get2();
    },
    // 修改按钮按下
    updateData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck("修改")) {
        //判断当前选中数据是否只有一条
        this.inforadmin = true; //更改显示，
        this.opreate = "update"; //将当前操作更改为修改
        vm.publicAddAndUpdate();
        this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]); //将选择中行的数据复制给addData，让form表单数据不为空
      }
    },
    //添加函数
    addDatas() {
      var vm = this;
      var stateId = vm.stateid;
      var data = vm.addData; // form表单的绑定数据
      var datas = {
        // 向数据接口传输的数据
        data: data,
        states: {
          id: stateId
        }
      };
      // 异步修改操作
      async function addDataFun() {
        var bool;
        if (vm.opreate == "update") {
          bool = vm.$api.http.opreateHttp( "", datas, "修改");
        } else if (vm.opreate == "add") {
          //添加操作
          bool = vm.$api.http.opreateHttp( "", datas, "添加");
        }
        return bool;
      }
      addDataFun().then(function(success) {
        if (success) {
          vm.inforAddClick(false);
        }
      });
    },
    // 删除按钮按下
    deleteData() {
      var vm = this;
      vm.$validator.errors.clear();
      var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA; // 获取当前页面的
      if (vm.$api.opreate.TESTcheck("删除")) {
        //询问框
        layer.confirm(
          "是否删除",
          {
            btn: ["确认", "取消"] //按钮
          },
          function() {
            var datas = {
              id: checkData[0].id, //需要删除行的数据id，需要根据行id进行相应修改 如选中行数据id为 userid,则此处需要改为checkData[0].userid
              states: {
                id: vm.stateid
              }
            };
            vm.$api.http.opreateHttp( "", datas, "删除");
          }
        );
      }
    },
    //搜索按钮按下
    searchBtnClick() {
      var vm = this;
      if (vm.searchData.data != "") {
      } else {
        vm.$layer.msg("请输入场景类型名后搜索", {
          icon: 0
        });
      }
    }
  }
};
</script>

<style>
</style>



